const createTreeNode = (value) => {
    return {
        data: value,
        parent: null,
        children: null
    }
}
const createTree = value => {
        return createTreeNode(value)
    }
    //ç»™èŠ‚ç‚¹æ·»åŠ æ•°æ®
const addChild = (node, value) => {
        //æ·»åŠ æ–°èŠ‚ç‚¹
        const newNode = {
            data: value,
            children: null,
            parent: node
        }
        node.children = node.children || []
        node.children.push(newNode)
        return newNode
    }
    //éå†èŠ‚ç‚¹ï¼Œå…ˆéå†ä¸€ä¸ªåˆ†æ”¯çš„å­èŠ‚ç‚¹
const travel = (tree, fn) => {
        //å½“æœ€åä¸€ä¸ªtree.childrenä¸å­˜åœ¨
        console.log(tree.data)
        fn(tree)
            //æœ€åä¸€ä¸ªèŠ‚ç‚¹æ—¶è·³å‡º
        if (!tree.children) {
            return
        }
        // fn(tree)
        for (let i = 0; i < tree.children.length; i++) {
            //éå†æ ¹èŠ‚ç‚¹çš„å­èŠ‚ç‚¹
            travel(tree.children[i], fn)
        }
    }
    //æŸ¥æ‰¾èŠ‚ç‚¹ å½“èŠ‚ç‚¹ä¸éœ€è¦æŸ¥æ‰¾çš„ç›¸åŒæ—¶ï¼Œè¿”å›æœ¬èº«ï¼Œå­èŠ‚ç‚¹å­˜åœ¨åˆ™é€’å½’éå†å­èŠ‚ç‚¹
const find = (tree, node) => {
    //å¦‚æœæŸ¥æ‰¾çš„èŠ‚ç‚¹ä¸æœ¬èº«ç›¸åŒåˆ™è¿”å›æœ¬èº«
    if (tree === node) {
        return tree
    } else if (tree.children) {
        //å¾ªç¯é€’å½’æŸ¥è¯¢æ ‘ç»“æ„ï¼Œå¦‚æœå­˜åœ¨nodeåˆ™è¿”å›nodeï¼Œä¸å­˜åœ¨åˆ™è¿”å›undefined
        for (let i = 0; i < tree.children.length; i++) {
            const result = find(tree.children[i], node)
            if (result) {

                return result
            }
        }
        return undefined
    } else {
        return undefined
    }
}

//åˆ é™¤æŒ‡å®šèŠ‚ç‚¹
const removeNode = (tree, node) => {
    const siblings = node.parent.children //æŸ¥æ‰¾åˆ°åˆ é™¤èŠ‚ç‚¹çš„åŒçº§èŠ‚ç‚¹
    let index = 0; //è®°å½•æŸ¥æ‰¾åˆ°nodeçš„ç´¢å¼•å€¼
    console.log(siblings)
        //å¾ªç¯éå†åŒçº§èŠ‚ç‚¹æŸ¥æ‰¾node
    for (let i = 0; i < siblings.length; i++) {
        if (siblings[i] === node) {
            index = i
        }
    }
    siblings.splice(index, 1) //æ•°ç»„åˆ é™¤æ–¹æ³•ï¼Œåˆ é™¤ä¸€ä¸ªæ•°æ®
}
